@import url(https://fonts.google.com/specimen/Roboto+Condensed?query=roboto+&preview.text_type=custom);
.pd {
    font-size: 24px; 
    padding-right: 20px;
    padding-left: 5px;
}

.input-field {
    outline: none;
    border: none;
    width: 80%;
    letter-spacing: 2px;
}

.container {
    font-size: 20px;
    /* border-radius: 50% !important; */
}

.outer-container {
    border: 1px solid;
    padding: 10px;
    border-radius: 50px;
    margin-bottom: 20px;
    margin-top: 20px;
    color: grey;
    border-color: grey;
}
.image-list {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
    grid-gap: 0px 10px;
    grid-auto-rows: 30px;
}

.image-list img {
    width: 350px;
    border-radius: 10px;
}

div {
    margin: auto;
}
